import React from 'react'

const FlightPage = (props) => {
    const {departure, destination, depart, passengers, adults, children, infant} = props.data
    return (
        <>
            <div>Departure: {departure}</div>
            <div>Destination: {destination}</div>
            <div>Date: {`${depart.getMonth()+1} ${depart.getDate()} ${depart.getFullYear()}`}</div>
            <div>Passengers: {passengers}</div>
            <div>Adults: {adults}</div>
            <div>Children: {children}</div>
            <div>Infant: {infant}</div>
        </>
    )
}
export default FlightPage